@import '../../../sass/variables';

.project {
  height: 47rem;
  perspective: 150rem;
  -moz-perspective: 150rem;
  position: relative;

  .side {
    transition: all 0.8s ease;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    backface-visibility: hidden;
    border-radius: 3px;
    overflow: hidden;
    box-shadow: 0 1.5rem 4rem rgba($color-black, 0.15);
  }

  .front {
    background-color: $color-grey-light-1;

    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: min-content min-content min-content;

    .projectPicture {
      width: 100%;
    }

    .projectTitle {
      width: 90%;
      justify-self: center;

      font-family: $font-display;
      font-size: 1.6rem;
      text-align: center;
      letter-spacing: 0.5rem;
      padding: 1.25rem;
      background-color: $color-tertiary-light;
      color: $color-white;
      font-weight: 400;
      box-shadow: 0 1.5rem 4rem rgba($color-black, 0.15);
      transform: translateY(-50%);

      @media only screen and (max-width: 56.25em) {
        font-size: 1.8rem;
      }

      @media only screen and (max-width: 32.8125em) {
        font-size: 2rem;
      }

      @media only screen and (max-width: 30em) {
        font-size: 2.3rem;
      }

      @media only screen and (max-width: 25.625em) {
        font-size: 2.4rem;
      }

      @media only screen and (max-width: 21.875em) {
        font-size: 2.45rem;
      }

      @media only screen and (max-width: 18.125em) {
        font-size: 2.45rem;
      }
    }

    .projectDetails {
      width: 100%;
      padding: 0 3rem 3rem 3rem;

      font-family: $font-primary;
      font-size: 1.6rem;
      text-align: justify;

      @media only screen and (max-width: 56.25em) {
        font-size: 1.8rem;
      }

      @media only screen and (max-width: 32.8125em) {
        font-size: 2rem;
      }

      @media only screen and (max-width: 30em) {
        font-size: 2.3rem;
      }

      @media only screen and (max-width: 25.625em) {
        font-size: 2.4rem;
      }

      @media only screen and (max-width: 21.875em) {
        font-size: 2.7rem;
      }

      @media only screen and (max-width: 18.125em) {
        font-size: 2.7rem;
      }
    }

    .projectButton {
      display: none;
    }
  }

  .back {
    transform: rotateY(180deg);
    // background-color: $color-grey-light-1;
    background-image: linear-gradient(
      to right bottom,
      $color-secondary-light,
      $color-secondary-dark
    );

    display: flex;
    justify-content: center;
    align-items: center;
  }

  &:hover .front {
    transform: rotateY(-180deg);
  }

  &:hover .back {
    transform: rotateY(0);
  }

  @media only screen and (max-width: 32.8125em), only screen and (hover: none) {
    height: auto;
    border-radius: 3px;
    background-color: $color-white;
    box-shadow: 0 1.5rem 4rem rgba($color-black, 0.15);
    transition: none;

    .side {
      height: auto;
      position: relative;
      border-radius: 3px;
      box-shadow: none;
    }

    .back {
      transform: rotateY(0deg);
      height: min-content !important;
    }

    &:hover .front {
      transform: rotateY(0deg);
    }
  }
}

